@font-face {
  font-family: "Neucha";
  src: url("../fonts/Neucha.woff2") format("woff2"), url("../fonts/Neucha.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-family: "Neucha", cursive;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  background-color: #658580;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow-x: hidden;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

li {
  list-style: none;
}

::-webkit-scrollbar {
  width: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

:root {
  --space-xxs: 0.25rem;
  --space-xs: 0.5rem;
  --space-sm: 1rem;
  --space-md: 1.5rem;
  --space-lg: 2rem;
  --space-xl: 3rem;
  --space-xxl: 6rem;
  --font-xl: 12rem;
  --item-size: calc(182.4px - var(--space-xs));
  --c-white: #ffffff;
  --c-white-1: #fafafa;
  --c-dark: #020202;
  --c-dark-1: #040404;
  --c-dark-2: #101010;
}

.site-container {
  background-color: #658580;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow-x: hidden;
  height: 100vh;
}

.about-us {
  width: 100vw;
  height: 100vh;
  font-size: 24px;
  line-height: 1.5;
  color: #ffffff;
  padding-top: 158px;
  padding-left: 80px;
  padding-right: 80px;
}

.about-us__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  margin-top: 40px;
}

.about-us__item {
  position: relative;
  padding-left: 35px;
  min-height: 40px;
  display: flex;
  align-items: center;
}

.about-us__item::after {
  content: "";
  position: absolute;
  bottom: 0;
  top: -4px;
  left: 0;
  margin: auto 0;
  width: 12px;
  height: 12px;
  border-radius: 50px;
  border: 2px solid #ffffff;
}

.about-us__subtitle {
  margin-bottom: 10px;
}

.about-us__text {
  margin-bottom: 15px;
}

.about-us__text span {
  color: #163631;
}

.header-page {
  padding-left: 40px;
  padding-right: 40px;
  z-index: 100;
  position: sticky;
  top: 0;
}

.header-page__navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 20px 0;
}

.header-page__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.header-page__item {
  height: min-content;
  width: 140px;
  margin-left: 15px;
}

.header-page__link {
  font-family: "Neucha", cursive;
  text-decoration: none;
  color: #ffffff;
  font-size: 24px;
  line-height: 1.5;
  padding: 25px;
  cursor: pointer;
  height: 100%;
  transition: color 2s step-start;
}
.header-page__link:hover {
  font-size: 28px;
}

.page-main {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 500vw;
  height: 100vh;
  overflow: hidden;
}

.preview {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 80px;
  padding-right: 80px;
  width: 100vw;
}

.preview__title {
  color: #ffffff;
  font-size: 56px;
  line-height: 1.8;
  width: 830px;
  text-align: center;
  letter-spacing: 0.15em;
}

.preview__title div {
  display: inline-block;
}

.preview__list {
  position: absolute;
  bottom: 100px;
  right: 40px;
  z-index: 100;
}

.preview__list svg {
  fill: #ffffff;
  transition: transform 0.5s ease-in, fill 0.8s ease-in;
}

.preview__link {
  width: fit-content;
  height: fit-content;
  cursor: pointer;
}

.preview__link:hover svg {
  fill: #163631;
  transform: scale(1.2);
}

.preview__background {
  display: block;
  opacity: 0.2;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: 1;
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.projects {
  background-color: #d67060;
  color: #ffffff;
  width: 100vw;
}

.projects h2 {
  position: relative;
  text-transform: uppercase;
  font-size: 82px;
  letter-spacing: 0.05em;
  line-height: 1.4;
  padding-left: 80px;
  margin-top: 100px;
  margin-bottom: 0;
}

.projects h2::after,
.projects h2::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  bottom: 0;
  left: 0;
}

.projects h2::after {
  top: 0;
}

.projects__container {
  height: calc(100vh - 200px);
  display: flex;
  overflow: hidden;
}

.projects__list-site {
  position: relative;
  display: flex;
  height: 100%;
  width: 100vw;
  min-width: 1400px;
  padding-left: 20vw;
  padding-right: 25px;
  background-color: #d67060;
  margin: 0;
}

.projects__list-site::before {
  content: "сайты";
  position: absolute;
  top: 0;
  left: calc(20vw - 80.64px);
  background-color: #ffffff;
  color: #163631;
  font-size: 18px;
  padding: 20px;
  z-index: 100;
}

.projects__list-site::after {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  min-width: 20vw;
  height: 100%;
  background-color: #163631;
  background-image: repeating-linear-gradient(transparent, transparent 60px, #ffffff 62px), repeating-linear-gradient(90deg, transparent, transparent 60px, #ffffff 62px);
}

.projects__subtitle {
  padding-top: 60px;
  margin-bottom: 40px;
  text-align: center;
  font-size: 1.5em;
}

.projects__img-container {
  position: relative;
  display: flex;
  height: 335px;
  width: 500px;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
}

.projects__img-container::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: radial-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.4));
}

.projects__project {
  width: 50vw;
  min-width: 50%;
  background-color: #d67060;
}

.projects__img-container:hover::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: none;
  transition: all 5s ease-in-out;
}

.projects__project-img {
  width: 345px;
  height: 90%;
  margin: 32px 110px 0 40px;
}

.projects__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 500px;
  height: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
}

.projects__side-image {
  width: 100%;
  height: 100%;
}

.projects__item {
  width: 100vw;
}

.projects__text {
  padding-left: 40px;
  padding-right: 40px;
  margin-bottom: 20px;
  font-size: 20px;
}

.projects__item-pet {
  position: relative;
  width: 500px;
  margin-left: calc(25vw - 270px);
  margin-right: calc(25vw - 270px);
  height: 600px;
}
.projects__item-pet:hover .projects__front {
  cursor: pointer;
  display: flex;
}

.projects__item-pet--three,
.projects__item-pet--four {
  width: 100vw;
}

.projects__pet-list {
  position: relative;
  padding: 80px 40px 60px;
  background-color: #163631;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  margin: 0;
}
.projects__pet-list::after {
  position: absolute;
  right: 0;
  left: 0;
  top: 0;
  margin: 0 auto;
  width: fit-content;
  content: "PET-projects";
  background-color: #d67060;
  color: #ffffff;
  font-size: 18px;
  padding: 20px;
}

.projects__side {
  width: 100%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.projects__front {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  margin: 0 auto;
  display: none;
  padding: 60px 60px 90px;
  background-color: rgba(0, 0, 0, 0.8);
  flex-direction: column;
}

.projects__side-title {
  font-size: 36px;
  margin-bottom: 20px;
}

.projects__side-text {
  font-size: 24px;
}

.projects__side-link {
  margin: auto auto 0;
  color: #ffffff;
  width: 250px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #ffffff;
  text-decoration: none;
  text-transform: uppercase;
}

.projects__pet-item {
  flex: 1;
}

.projects__pet-link {
  font-family: "Neucha", cursive;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 60px;
  color: transparent;
  -webkit-text-stroke: 1px #ffffff;
}
.projects__pet-link:hover {
  color: #ffffff;
}

.projects__pet {
  width: 100vw;
  height: 100%;
  background-color: #163631;
  overflow: hidden;
}

.projects__pet h3 {
  margin: 0;
}

.projects__content {
  width: 100vw;
}

.projects__link-git {
  padding: 40px;
  font-family: "Neucha", cursive;
  color: #163631;
  font-size: 24px;
  margin-top: 25px;
}
.projects__link-git:hover {
  color: #ffffff;
  font-size: 30px;
}

.skills {
  margin-top: 50px;
}

:root {
  --space-xxs: 0.25rem;
  --space-xs: 0.5rem;
  --space-sm: 1rem;
  --space-md: 1.5rem;
  --space-lg: 2rem;
  --space-xl: 3rem;
  --space-xxl: 6rem;
  --font-xl: 4rem;
  --item-size: calc(182.4px - var(--space-xs));
  --c-white: #ffffff;
  --c-white-1: #fafafa;
  --c-dark: #020202;
  --c-dark-1: #040404;
  --c-dark-2: #101010;
}

.about-us-link.is-active {
  color: #163631;
  font-size: 28px;
}

.content {
  block-size: 100%;
  display: block;
  position: relative;
}

.scroll-content {
  position: relative;
  block-size: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  padding: 40px;
}

.item-container-full {
  position: absolute;
  right: 0;
  height: var(--item-size);
  overflow: hidden;
}

.item-container-transparent {
  z-index: 100;
  width: fit-content;
  margin-left: auto;
  padding-right: 80px;
}

.item {
  z-index: 100;
  line-height: 0.95;
  display: flex;
  justify-content: end;
  width: fit-content;
  margin-left: auto;
}

.title-full {
  color: var(--c-white);
}

.title {
  font-size: var(--font-xl);
  text-transform: uppercase;
  text-align: right;
  display: block;
  line-height: 1;
  width: fit-content;
  margin-left: auto;
}

.title-transparent {
  -webkit-text-stroke: 1px var(--c-white);
  color: transparent;
}

img {
  inline-size: 100%;
  block-size: 100%;
  display: block;
  object-fit: cover;
}

.tv {
  width: 100%;
  height: 100%;
  object-fit: cover;
  overflow: hidden;
  background-color: #cccccc;
  border-radius: 7px;
}
.tv::after {
  position: absolute;
  left: -50%;
  top: -60%;
  background-image: url("../image/tv-noice.png");
  pointer-events: none;
  animation: grain 1s steps(15) infinite;
  will-change: background-position;
  backface-visibility: visible;
  transform: translateZ(0);
  content: "";
  width: 166%;
  height: 200%;
  z-index: 60;
}
.tv::before {
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  width: 20px;
  content: "";
  background-color: #cccccc;
}

.tv.js-not-after {
  background-color: transparent;
}
.tv.js-not-after::after {
  display: none;
}

.pet__picture {
  width: 700px;
  height: 470px;
  position: absolute;
  overflow: hidden;
  left: 8%;
  bottom: 0;
  top: 0;
  margin: auto;
}

.picture {
  width: 95%;
  height: 95%;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 2%;
}

.pet__image-tv {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
}

.img-select {
  display: none;
  opacity: 0;
  transition: all 0.5s ease-in-out;
  width: 665px;
  height: 447px;
}

.img-select.is-active {
  display: block;
  opacity: 1;
}

@keyframes grain {
  0%, 100% {
    transform: translate(0, 0);
  }
  10% {
    transform: translate(-15%, -10%);
  }
  20% {
    transform: translate(-20%, -5%);
  }
  30% {
    transform: translate(7%, -25%);
  }
  40% {
    transform: translate(-5%, 25%);
  }
  50% {
    transform: translate(-20%, 10%);
  }
  60% {
    transform: translate(25%, 0%);
  }
  70% {
    transform: translate(0%, 15%);
  }
  80% {
    transform: translate(3%, 35%);
  }
  90% {
    transform: translate(-10%, 10%);
  }
}
@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(7px);
  }
  100% {
    transform: translateY(0);
  }
}
.contacts {
  position: relative;
  width: 100vw;
  height: 100%;
  padding: 120px 80px 100px;
  background-color: #cccccc;
  display: flex;
  flex-direction: column;
  color: #ffffff;
}
.contacts::after {
  position: absolute;
  left: -100%;
  top: -100%;
  background-image: url("../image/tv-noice.png");
  pointer-events: none;
  animation: grain 1s steps(15) infinite;
  will-change: background-position;
  backface-visibility: visible;
  transform: translateZ(0);
  content: "";
  width: 366%;
  height: 300%;
  z-index: -60;
}

.contacts__desk-link {
  font-size: 3em;
  line-height: 1.2;
  text-align: center;
}

.contacts__link-email {
  font-family: "Neucha", cursive;
  color: #d67060;
  font-size: 2.4em;
  line-height: 1.2;
  display: block;
  margin: 0 auto 50px;
  text-align: center;
}

.contacts__title {
  font-size: 6.5em;
  line-height: 1.2;
  padding: 0;
  margin: 0 auto 25px;
  text-align: center;
}

.contacts__form {
  font-family: "Neucha", cursive;
  font-size: 2em;
  color: #ffffff;
}

.contacts__form-title {
  margin-bottom: 10px;
  line-height: 1.2;
  margin-left: auto;
  margin-right: auto;
}

.contacts__field {
  border: none;
  border-radius: 5px;
  margin-top: 10px;
}

.contacts__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 40px;
}

.contacts__field-container {
  display: flex;
  align-items: center;
}
.contacts__field-container:first-child {
  justify-self: end;
}

.contacts__field-container--text {
  grid-row: 2/3;
  grid-column: 1/3;
  justify-self: center;
  font-size: 0.6em;
  padding: 5px;
  color: #000000;
  min-height: 120px;
}

.contacts__field-desc {
  padding: 0 35px 0 0;
  line-height: 1.2;
  color: #d67060;
  font-size: 36px;
}

.contacts input {
  margin: 0;
  padding: 5px 20px;
  color: #163631;
  font-family: "Neucha", cursive;
  line-height: 1.1;
  font-size: 1em;
  outline: none;
}
.contacts input:focus {
  color: #163631;
}

.contacts__information-button,
.contacts button {
  font-family: "Neucha", cursive;
  padding: 7px 20px;
  color: #d67060;
  border: none;
  border-radius: 5px;
  background-color: #fafafa;
  margin-top: 40px;
  min-width: 175px;
  display: block;
  cursor: pointer;
  text-align: center;
}
.contacts__information-button:hover,
.contacts button:hover {
  color: #163631;
}

.contacts__button {
  margin-left: auto;
  margin-right: auto;
}

.contacts textarea {
  width: 500px;
  height: 120px;
  color: #163631;
  font-size: 18px;
  outline: none;
  padding: 8px;
}

.contacts__information {
  display: grid;
  grid-template-columns: repeat(2, min-content);
  gap: 25px;
}

@keyframes grain {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(0, 0);
  }
  10% {
    transform: translate(-15%, -10%);
  }
  20% {
    transform: translate(-20%, -5%);
  }
  30% {
    transform: translate(7%, -25%);
  }
  40% {
    transform: translate(-5%, 25%);
  }
  50% {
    transform: translate(-20%, 10%);
  }
  60% {
    transform: translate(25%, 0%);
  }
  70% {
    transform: translate(0%, 15%);
  }
  80% {
    transform: translate(3%, 35%);
  }
  90% {
    transform: translate(-10%, 10%);
  }
}
